.message {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 1em;
  border: 1px solid #000;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 2em;
  border-radius: 5px;
  gap: 10px;
  position: relative;
}

.success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
  font-style: italic;
  text-transform: capitalize;
}

.error {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
  font-style: italic;
  text-transform: capitalize;
}
